<template>
  <div>
    <SearchFour />
    <div class="sidebar">
      <div class="bg">
        <van-sidebar v-model="activeKey" class="sidelist">
          <van-sidebar-item
            class="sidebaritem"
            :title="s.title"
            v-for="(s, i) in sidebar"
            :key="i"
            :class="{
              active: i == icon,
            }"
            @click="
              SecondaryEl({ pid: s.maitKey ? s.maitKey : 3627 }),
                mainlistEl({ fcid: s.miniWallkey, sort }),
                (icon = i)
            "
          />
        </van-sidebar>
      </div>

      <div class="goods">
        <ul class="top">
          <li
            v-for="(s, i) in Secondary"
            :key="i"
            @click="$router.push({ path: '/bargoodlist',query: { q: s.title, sort: 'pop' } })
            "
          >
                   <!-- $router.push({ path: '/bargoodlist' }),
                SearchResult({ q: s.title, sort: 'pop' }) -->
            <img :src="s.image" alt="" />
            <p>{{ s.title }}</p>
          </li>
        </ul>
        
        <ul class="mainnav">
          <li
            :class="{
              active: mainnavs[0]?.sortKey == sort,
            }"
            @click="mainlistEl({ fcid, sort: mainnavs[0]?.sortKey })"
          >
            {{ mainnavs[0]?.title }}
          </li>
          <span>|</span>
          <li
            :class="{
              active: mainnavs[1]?.sortKey == sort,
            }"
            @click="mainlistEl({ fcid, sort: mainnavs[1]?.sortKey })"
          >
            {{ mainnavs[1]?.title }}
          </li>
          <span>|</span>
          <li
            :class="{
              active: mainnavs[2]?.sortKey == sort,
            }"
            @click="mainlistEl({ fcid, sort: mainnavs[2]?.sortKey })"
          >
            {{ mainnavs[2]?.title }}
          </li>
        </ul>

        <ul class="mainlist">
          <li
            v-for="(mainlist, i) in mainlists"
            :key="i"
            @click="
            $router.push({ path: '/goodspage', query:{itemId: mainlist.iid } })
            "
          >
            <div
              class="img"
              :style="{
                backgroundImage:
                  'url(' + `${mainlist.img}_280x374.v1cAC.40.webp` + ')',
              }"
            ></div>
            <p class="title">{{ mainlist.title }}</p>
            <div class="price">
              <span>￥{{ mainlist.price }}</span>
              <p>{{ mainlist.sale }} <i></i></p>
            </div>
          </li>
          <!-- :style="{
                backgroundImage:
                  'url(' +
                  `${mainlist.img}?imageView=1&type=webp&thumbnail=50x0` +
                  ')',
              }" -->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import SearchFour from "@/components/SearchFour.vue";
import { mapActions } from "vuex";
export default {
  components: {
    SearchFour,
  },
  data() {
    return {
      activeKey: 0,
      icon: "",
      sidebar: [],
      Secondary: [],
      mainnavs: [],
      mainlists: [],
      sidebarlist: [],
    };
  },

  methods: {
    ...mapActions(["SearchResult"]),
    // 商品主分类
    sidebarEl() {
      this.axios
        .get("https://apis.netstart.cn/mogujie/category.json")
        .then((res) => {
          this.sidebar = res.data.data["117330"].list;
        });
    },

    //商品二级导航
    SecondaryEl({ pid }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/category/getSub", {
          params: { pid },
        })
        .then((res) => {
          this.Secondary = res.data.data.list;
        });
    },

    //（主分类商品列表）
    mainlistEl({ fcid, sort }) {
      this.fcid = fcid;
      this.sort = sort;
      // console.log(fcid, sort);
      this.axios
        .get("https://apis.netstart.cn/mogujie/category/miniWall", {
          params: { fcid, sort },
        })
        .then((res) => {
          this.mainnavs = res.data.result.sortFilter;
          this.mainlists = res.data.result.wall.docs;
        });
    },
  },

  created() {
    this.sidebarEl();
    this.SecondaryEl({ pid: 3627 });
    this.mainlistEl({ fcid: 10062603, sort: "pop" });
  },
};
</script>

<style lang="less" scoped>
.sidebar {
  position: relative;
  top: 30rem;
  left: 0;
  .bg::-webkit-scrollbar {
    display: none;
  }
  .bg {
    height: 100%;
    position: fixed;
    top: 51.5rem;
    left: 0;
    overflow-x: hidden;
    overflow-y: scroll;
    .sidelist {
      &.sidebaritem {
        color: red;
        font-weight: bolder;
        font-size: 15rem;
      }
    }
  }

  .goods {
    width: 295rem;
    position: absolute;
    top: 0;
    right: 0;

    .top {
      display: flex;
      flex-wrap: wrap;
      color: rgb(80, 80, 80);
      li {
        margin: 10rem 14rem;
        img {
          width: 70rem;
          height: 70rem;
        }
      }
    }
    .mainnav {
      width: 295rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 0.5rem solid rgb(220, 220, 220);
      margin-top: 10rem;
      li {
        width: 98rem;
        padding: 10rem 0;
        text-align: center;
        font-size: 14.5rem;
        &.active {
          color: red;
        }
      }
      span {
        color: rgb(185, 185, 185);
      }
    }
    .mainlist {
      width: 295rem;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
      //   background: #efefef;
      padding: 8rem 0rem;
      li {
        width: 135rem;
        // height: 260rem;
        background: rgb(255, 255, 255);
        margin-bottom: 10rem;
        border-radius: 5rem;
        padding-bottom: 5rem;
        .img {
          width: 135rem;
          height: 175rem;
          background-size: cover;
          background-repeat: no-repeat;
          border-radius: 5rem 5rem 0 0;
        }
        .title {
          width: 125rem;
          height: 18rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          margin: 3rem auto;
        }
        .price {
          width: 125rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin: 5rem auto;
          span {
            font-size: 15rem;
            font-weight: bold;
          }
          p {
            display: flex;
            align-items: center;
            color: rgb(108, 108, 108);
            i {
              width: 13rem;
              height: 13rem;
              background-image: url(../assets/mini-star.png);
              background-repeat: no-repeat;
              background-size: 100%;
              display: inline-block;
              margin-left: 3rem;
            }
          }
        }
      }
    }
  }
  // .active {
  //   color: red;
  // }
}
</style>