<template>
  <ul>
    <li
      v-for="(n, i) in navdata"
      :key="i"
      @click="
        $router.push({ path: '/searchview' }),
          SearchResult({ q: n.title, sort: 'pop' }),
          savehistoryEl(),
          openEl({ ifopen: false })
      "
    >
      <img :src="n.image" alt="" />
      <span>{{ n.title }}</span>
    </li>
  </ul>
</template>

<script>
import { mapMutations, mapActions } from "vuex";
export default {
  data: function () {
    return {
      navdata: [],
    };
  },

  methods: {
    ...mapMutations(["openEl", "savehistoryEl"]),
    ...mapActions(["SearchResult"]),
  },

  created() {
    //首页导航
    this.axios
      .get("https://apis.netstart.cn/mogujie/homeNav.json")
      .then((res) => {
        this.navdata = res.data;
      });
  },
};
</script>

<style lang="less" scoped>
ul {
  width: 375rem;
  height: 155rem;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  li {
    width: 60rem;
    margin: 5rem 5rem;
    img {
      width: 50rem;
    }
  }
}
</style>