<template>
  <div class="searchview">
    <div v-if="open">
      <!-- 搜索 -->
      <div class="search">
        <span @click="$router.back()"></span>
        <div class="input" :class="{ active: view }">
          <input
            type="text"
            v-model="value"
            ref="focus"
            :placeholder="history[0]"
            @keydown.enter="
              SearchResult({ q: value ? value : history[0], sort: 'pop' }),
                savehistoryEl(),
                openEl({ ifopen: false })
            "
          />
        </div>
        <p
          @click="
            SearchResult({ q: value ? value : history[0], sort: 'pop' }),
              savehistoryEl(),
              openEl({ ifopen: false })
          "
        >
          搜索
        </p>
      </div>
      <!-- 历史记录 -->
      <div class="history">
        <p><i></i>历史搜索</p>
        <span @click="clearhistory(),savehistoryEl()"></span>
      </div>
      <!-- 历史记录 -->
      <ul class="searched">
        <p v-if="history.length == 0">暂无历史搜索</p>
        <li
          v-for="(h, i) in history"
          :key="i"
          @click="
            SearchResult({ q: h, sort: 'pop' }),
              openEl({ ifopen: false }),
              (value = h)
          "
        >
          {{ h }}
        </li>
      </ul>
      <!-- 热搜 -->
      <div class="hotsearch">
        <p><i></i>热门搜索</p>
        <ul class="hoticon">
          <li
            v-for="(hot, index) in hotSearch"
            :key="index"
            :style="{ color: hot.color }"
            @click="
              SearchResult({ q: hot.query, sort: 'pop' }),
                savehistoryEl(),
                openEl({ ifopen: false }),
                (value = hot.query)
            "
          >
            {{ hot.query }}
          </li>
        </ul>
      </div>
    </div>
    <GoodList />
  </div>
</template>

<script>
import GoodList from "@/components/GoodList.vue";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  components: {
    GoodList,
  },
  data: function () {
    return {
      view: false,
      value: "",
      hotSearch: [],
    };
  },
  computed: {
    ...mapState(["open", "history"]),
  },
  methods: {
    ...mapMutations(["openEl", "clearhistory", "savehistoryEl"]),
    ...mapActions(["SearchResult"]),
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/mogujie/hotSearch.json")
      .then((res) => {
        this.hotSearch = res.data.data.hotWord.data;
      });
    // this.$store.dispatch("SearchResult",{q, sort, minPrice, maxPrice})
  },
  mounted() {
    if (this.open) {
      this.$refs.focus.focus();
    }
    this.view = true;
  },
};
</script>

<style lang="less" scoped>
.searchview {
  width: 375rem;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  background: rgb(255, 255, 255);
  .search {
    width: 375rem;
    display: flex;
    align-items: center;
    padding: 10rem;
    box-sizing: border-box;
    box-shadow: 0 0 1rem 0 rgb(192, 192, 192);

    span {
      width: 25rem;
      height: 25rem;
      background-image: url(../assets/searchback.png);
      background-size: 95%;
      background-position: -1.5rem 1rem;
      background-repeat: no-repeat;
    }
    .input {
      width: 290rem;
      margin: 0rem auto;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 5rem;
      padding: 1rem 5rem;
      border: 1rem solid rgb(168, 168, 168);
      &.active {
        border: 1rem solid #ff839b;
        z-index: 9;
      }
      input {
        width: 300rem;
        height: 22rem;
        border: none;
        // background: #ebecec;
        outline: none;
      }
    }

    p {
      width: 30rem;
      height: 30rem;
      background-size: 100%;
      background-repeat: no-repeat;
      display: inline-block;
      line-height: 30rem;
      font-size: 14rem;
    }
  }
  .history {
    width: 340rem;
    margin: 20rem 10rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: gray;
      i {
        width: 15rem;
        height: 15rem;
        display: inline-block;
        background-image: url(../assets/搜搜icon.png);
        background-size: 100%;
        background-repeat: no-repeat;
        margin-right: 5rem;
      }
    }
    span {
      width: 15rem;
      height: 15rem;
      display: inline-block;
      background-image: url(../assets/delete.png);
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }
  .searched {
    width: 350rem;
    margin: 10rem auto;
    padding-bottom: 20rem;
    border-bottom: 1rem solid rgb(238, 238, 238);
    display: flex;
    justify-content: left;
    align-items: center;
    flex-wrap: wrap;
    p {
      text-align: left;
      color: gray;
      margin-left: 15rem;
    }
    li {
      height: 25rem;
      line-height: 25rem;
      border-radius: 3rem;
      border: 1px solid #f5f5f5;
      padding: 0 10rem;
      margin: 5rem;
    }
  }
  .hotsearch {
    p {
      padding: 10rem 0;
      text-align: left;
      display: flex;
      align-items: center;
      margin: 0rem 10rem;
      color: gray;
      i {
        width: 15rem;
        height: 15rem;
        background-image: url(../assets/mini-star.png);
        background-repeat: no-repeat;
        background-size: 100%;
        display: inline-block;
        margin-right: 5rem;
      }
    }
    .hoticon {
      width: 350rem;
      display: flex;
      justify-content: left;
      align-items: center;
      flex-wrap: wrap;
      margin: 0 auto;
      li {
        height: 25rem;
        line-height: 25rem;
        border-radius: 3rem;
        border: 1px solid #f5f5f5;
        padding: 0 10rem;
        margin: 5rem;
      }
    }
  }

  // .active {
  //   border: 1rem solid #ff839b;
  //   background: pink;
  //   z-index: 9;
  // }
}
</style>