import Vue from 'vue'
import Vuex from 'vuex'
const axios = Vue.axios

Vue.use(Vuex);

export default new Vuex.Store({

  // 全局变量:data
  state: {
    results: [],
    open: true,
    all: {},
    priceFilter: [],
    navFilter: [],
    history: JSON.parse(localStorage.getItem("mogujiehistory")) || [],
    resultsort: '',
    shoppingcar: JSON.parse(localStorage.getItem("shoppingcar")) || [],
    livingroom: {},
    buynows: [],
    orderlist: JSON.parse(localStorage.getItem("orderlist")) || [],
    collectional: JSON.parse(localStorage.getItem("collectional")) || [],

  },

  // 计算属性(不支持点击事件)
  getters: {
  },

  // 方法
  mutations: {
    // 本地存储搜索历史记录
    savehistoryEl: function (state) {
      localStorage.setItem("mogujiehistory", JSON.stringify(state.history));
      localStorage.setItem("shoppingcar", JSON.stringify(state.shoppingcar));
      localStorage.setItem("orderlist", JSON.stringify(state.orderlist));
      localStorage.setItem("collectional", JSON.stringify(state.collectional));
    },

    // 删除搜索历史记录
    clearhistory: function (state) {
      state.history = [];
    },

    openEl(state, { ifopen }) {
      return state.open = ifopen;
    },

    copyEl(state, { all }) {
      state.all = all;
    },

    resultsEl(state, data) {
      state.results = data;
    },

    historyEl(state, data) {
      state.history = [...new Set([data, ...state.history])];
    },

    navFilterEl(state, data) {
      state.navFilter = data;
    },

    priceFilterEl(state, data) {
      state.priceFilter = data;
    },

    resultsortEl(state, data) {
      state.resultsort = data;
    },

    livingroomEl(state, data) {
      state.livingroom = data;
      console.log(state.livingroom);
    },

    // 立即购买
    buynowEl(state, { all }) {
      state.buynows = [all, ...state.buynows,];
      console.log("立即购买", state.buynows);
    },

    //退出时，将立即购买的内容清空
    clearbuynowEl(state) {
      state.buynows = []
      console.log("立即购买", state.buynows);
    },

    // 购物车
    shoppingcarEl(state, { all }) {
      state.shoppingcar = [all, ...state.shoppingcar,];
      console.log("购物车", state.shoppingcar);
    },

    // 单选
    prefinishEl(state, { id, finish }) {
      state.shoppingcar = state.shoppingcar.map((o) => (o === id ? { ...o, finish } : o));
    },
    // 全选
    allfinishEl(state, { finish }) {
      state.shoppingcar = state.shoppingcar.map((o) => ({
        ...o,
        finish,
      }));
    },

    // 删除购物车商品
    deletecarEl(state) {
      state.shoppingcar = state.shoppingcar.filter((o) => (o.finish === false));
    },

    //订单列表
    orderlistEl(state, { list }) {
      state.orderlist = list.concat(state.orderlist);
      console.log("订单列表", state.orderlist);
    },

    // 删除订单列表商品
    deleteorderEl(state, { list }) {
      state.orderlist = state.orderlist.filter((o) => (o !== list));
    },

    // 收藏
    collectionEl(state, { collection, ifcollect }) {
      // 添加
      if (ifcollect) {
        state.collectional = [...new Set([collection, ...state.collectional])];
        console.log(state.collectional);
      } else {
        // 删除
        state.collectional = state.collectional.filter((o) => (o !== collection));
        console.log(state.collectional);
      }
    },

    // 取消收藏
    deleteCollectionEl(state, { id }) {
      state.collectional = state.collectional.filter((o) => (o !== id));
      console.log(state.collectional);
    },
  },

  // 异步
  actions: {
    // 搜索结果
    SearchResult(context, { q, sort, minPrice, maxPrice, page }) {
      axios.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
        params: { q, sort, minPrice, maxPrice, page }
      })
        .then((res) => {
          // console.log(res.data.result.wall.docs);
          context.commit("historyEl", q);
          context.commit("resultsortEl", sort);
          context.commit("resultsEl", res.data.result.wall.docs);
          context.commit("navFilterEl", res.data.result.sortFilter);
          context.commit("priceFilterEl", res.data.result.priceFilter);
        });
    },

    //直播间(直播链接, 直播详情)
    livingroomEl(context, { roomId, actorId }) {
      axios.get("https://apis.netstart.cn/mogujie/live/actorLiveInfo", {
        params: { roomId, actorId }
      })
        .then((res) => {
          console.log(res.data.data);
          context.commit("livingroomEl", res.data.data);
          // state.livingroom = res.data.data;
        });
    },

  },

  modules: {
  }
})
