<template>
  <div class="Live">
    <header>
      <div class="left">
        <img src="../assets/mogu-logo.png" alt="" />
        <div class="text">
          <h2>蘑菇街 总有高手帮你挑</h2>
          <p>
          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <span>超过2亿用户已下载</span>
          </p>
        </div>
      </div>
      <div class="btn">前往查看</div>
    </header>
    <ul class="nav">
      <li v-for="(nav, i) in livenav" :key="i" :class="{activecolor: k== nav.id}"
        @click="livinglistEl({ tabId: nav.id, pageIndex: 1 }), k=nav.id">{{nav.name}}</li>
    </ul>
    <ul class="livelist">
      <li class="preitem" v-for="(list, i) in livelist" :key="i"
        @click="$router.push({ path: '/livepage',query: {roomId:list.roomId, actorId:list.actUserId} })">
        <div class="top">
          <div class="photo" :style="{ backgroundImage:'url(' + `${list.avatar}_280x374.v1cAC.40.webp` + ')' }"></div>
          <span>{{list.userName}}</span>
        </div>
        <div class="bg" :style="{backgroundImage:'url(' + `${list.bgImg}` + ')', }">
          <div class="infor">
            <div class="living">
              <div class="icon"></div>
              <span>直播中 |</span>
              <p><i></i>{{(list.visitorCount / 10000).toFixed(1)}}W</p>
            </div>
            <div class="text">{{list.intro}}</div>
            <div class="fixed">
              <ul class="goods">
                <li v-for="(goods, i) in list.shopCarts.goods" :key="i"
                  :style="{ backgroundImage:'url(' + `${goods.cover}_280x374.v1cAC.40.webp` + ')' }">
                  <span>{{goods.price}}</span></li>
              </ul>
              <ul class="comments">
                <li></li>
              </ul>
            </div>
          </div>
          <div class="mask"></div>
        </div>
      </li>
      <p :style="{ marginBottom: '35rem' }"></p>
    </ul>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data: function () {
    return {
      k: 1,
      livenav: [],
      livelist: [],
    };
  },

  computed: {},

  methods: {
    ...mapActions(["livingroomEl"]),

    // 直播导航
    livenavEl() {
      this.axios
        .get("https://apis.netstart.cn/mogujie/live/tabHeader")
        .then((res) => {
          this.livenav = res.data.data.tabs.items;
        });
    },

    // 直播列表
    livinglistEl({ tabId, pageIndex }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/live/tabList", {
          params: { tabId, pageIndex },
        })
        .then((res) => {
          this.livelist = res.data.data.lives;
        });
    },
  },

  created() {
    this.livenavEl();
    this.livinglistEl({ tabId: 1, pageIndex: 1 });
  },
};
</script>

<style lang="less">
.Live {
  width: 375rem;
  overflow: hidden;

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 7rem 10rem;
    border-bottom: 0.5rem solid rgb(199, 199, 199);

    .left {
      display: flex;
      justify-content: space-between;
      align-items: center;

      img {
        width: 32rem;
        height: 32rem;
        margin-right: 5rem;
      }

      .text {
        text-align: left;

        h2 {
          font-size: 13rem;
        }

        p {
          display: flex;
          justify-content: space-between;
          margin-top: 2rem;

          ul {
            li {
              width: 9rem;
              height: 9rem;
              display: inline-block;
              background-image: url(../assets/星集.png);
              background-size: 100%;
              background-repeat: no-repeat;
              margin: 0 2rem;
            }
          }

          span {
            margin-left: 7rem;
            color: rgb(168, 168, 168);
          }
        }
      }
    }

    .btn {
      padding: 4rem 12rem;
      background: #ff4466;
      border-radius: 3rem;
      color: #fff;
    }
  }

  .nav {
    width: 375rem;
    height: 40rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    // background: rgb(223, 223, 223);
    li {
      width: 25%;
      line-height: 40rem;
      font-size: 16rem;
      border-bottom: 2rem solid transparent;

      &.activecolor {
        border-bottom: 2rem solid #ff4466;
        color: #ff4466;
      }
    }
  }

  .livelist {
    .preitem {
      width: 350rem;
      margin: 10rem auto;

      .top {
        display: flex;
        align-items: center;
        position: relative;
        top: 15rem;
        left: 15rem;

        .photo {
          width: 50rem;
          height: 50rem;
          border-radius: 50%;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
          border: 2rem solid rgb(255, 255, 255);
          margin-right: 7rem;
        }

        span {
          font-size: 16rem;
          position: relative;
          top: -5rem;
          left: 0;
          color: gray;
        }
      }

      .bg {
        width: 350rem;
        height: 220rem;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        border-radius: 5rem;
        position: relative;
        text-align: left;
        z-index: -1;

        .infor {
          position: absolute;
          bottom: 10rem;
          left: 10rem;
          z-index: 1;

          .living {
            width: 115rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            background: #51515189;
            box-sizing: border-box;
            padding: 1rem 5rem;
            border-radius: 3rem;
            color: #fff;

            @keyframes iconactive {
              0% {
                opacity: 1;
              }

              100% {
                opacity: 0;
              }
            }

            .icon {
              width: 8rem;
              height: 8rem;
              border-radius: 50%;
              background: red;
              // margin-right: 3rem;
              animation: iconactive 1s linear 0s infinite alternate-reverse;
            }

            span {}

            p {
              i {
                width: 10rem;
                height: 10rem;
                display: inline-block;
                background-image: url(../assets/fire.png);
                background-size: 100%;
              }
            }
          }

          .text {
            font-size: 16rem;
            color: #f5f4f5;
            margin: 7rem 0;
          }

          .fixed {
            .goods {
              width: 205rem;
              // background: tomato;
              display: flex;
              justify-content: space-between;

              li {
                width: 65rem;
                height: 65rem;
                background-size: 100%;
                background-repeat: no-repeat;
                position: relative;

                span {
                  position: absolute;
                  bottom: 0;
                  left: 5rem;
                  font-size: 14.5rem;
                  color: #fcfafa;
                  margin: 0;
                }
              }
            }

            .comments {
              li {}
            }
          }
        }

        .mask {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background: rgba(0, 0, 0, 0.3);
          border-radius: 5rem;
          z-index: 0;
        }
      }
    }
  }
}
</style>