<template>
  <div>
    <div class="nowbuy">
      <div class="toptitle">
        <span @click="$router.back()"></span>
        <p>确认订单</p>
        <i class="fa fa-ellipsis-h"></i>
      </div>
      <van-cell class="tikview" is-link @click="showPopup">
        <div class="write">
          <van-icon size="25" name="location-o" />
          <div class="text">
            <p>大蘑菇与小蘑菇 <span> 18566666666</span></p>
            <p class="dizhi">蘑菇市蘑菇区大湾村蘑菇街666号</p>
          </div>
        </div>
      </van-cell>

      <ul>
        <li class="goodinfor" v-for="(buynow, i) in buynows[0]" :key="i">
          <div class="storetop">
            <h3>
              <van-icon size="20" name="shop-o" /> {{ buynow?.seller }}
            </h3>
            <span>联系店家</span>
          </div>
          <div class="gooditem">
            <div class="goodphoto" :style="{
              backgroundImage: 'url(' + `${buynow?.img}` + ')',
            }"></div>
            <div class="text">
              <div class="actor">
                <div class="img" :style="{
                  backgroundImage: 'url(' + `${buynow?.logo}` + ')',
                }"></div>
                <p>{{ buynow?.seller }}</p>
              </div>
              <h5>{{ buynow?.text }}</h5>
              <div class="Sizecolor">
                <span v-if="buynow?.color">颜色:{{ buynow?.color }};</span><span v-if="buynow?.sise"> 尺码:{{ buynow?.sise
                }}</span>
              </div>
            </div>
            <div class="price">
              <p>
                ￥{{
                buynow?.price
                .toString()
                .substring(0, buynow?.price.toString().length - 2)
                }}
              </p>
              <!-- <p>￥{{ buynow?.price / 100 }}</p> -->
              <van-stepper v-model="buynow.num" class="adds" />
            </div>
          </div>
        </li>
      </ul>

      <div class="remark" :style="{ marginBottom: '55rem' }">
        <span>订单备注</span>
        <div class="note">
          <input type="text" placeholder="有什么想说的可以写在这里哦~" />
        </div>
      </div>
      <div class="all">
        <p>
          总计:
          <span>￥{{ allnum() }}</span>
        </p>

        <div class="btn">
          <span>共{{ buynows[0].length }}件商品</span>
          <p @click="
            $router.push({ path: '/orderpage', query: { current: 'send' } }),
              orderlistEl({ list: buynows[0] })
          ">
            提交订单
          </p>
        </div>
      </div>
      <van-popup v-model="show" position="bottom" :style="{ height: '71%' }">
        <van-address-list :list="list" default-tag-text="默认" />
      </van-popup>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data: function () {
    return {
      value: "",
      nowbuygood: {},
      show: false,
      list: [
        {
          id: '1',
          name: '大蘑菇与小蘑菇',
          tel: '18566666666',
          address: '蘑菇市蘑菇区大湾村蘑菇街666号',
          isDefault: true,
        },
        {
          id: '2',
          name: '采了蘑菇,丢了芝麻忘记开门',
          tel: '1310000000',
          address: '浙江省杭州市拱墅区莫干山路 50 号',
        },
      ],
    };
  },
  computed: {
    ...mapState(["buynows"]),
  },
  methods: {
    ...mapMutations(["clearbuynowEl", "orderlistEl"]),
    allnum: function () {
      return this.buynows[0]
        .map((o) => o.price * o.num)
        .reduce((total, num) => total + num)
        .toString()
        .substring(
          0,
          this.buynows[0]
            .map((o) => o.price * o.num)
            .reduce((total, num) => total + num)
            .toString().length - 2
        );
    },
    showPopup() {
      this.show = true;
    },
  },
};
</script>

