<template>
  <div class="collectional">
    <div class="title">
      <i class="fa fa-angle-left" @click="$router.push({ path: '/user' })"></i>
      <p>我的收藏</p>
      <span @click="$router.push({ path: '/shoppingcar' })"></span>
    </div>
    <ul class="collect" v-if="collectional.length > 0">
      <li
        class="list"
        v-for="(list, i) in collectional"
        :key="i"
        @click="
          $router.push({
            path: '/goodspage',
            query: { itemId: list.itemInfo.itemId },
          })
        "
      >
        <van-swipe-cell>
          <div class="storetop">
            <div
              class="img"
              :style="{
                backgroundImage: 'url(' + `${list?.shopInfo?.shopLogo}` + ')',
              }"
            ></div>
            <h3>{{ list?.shopInfo?.name }}</h3>
          </div>
          <div class="gooditem">
            <div
              class="goodphoto"
              :style="{
                backgroundImage:
                  'url(' + `${list?.skuInfo?.skus[0]?.img}` + ')',
              }"
            ></div>
            <div class="text">
              <h5>{{ list?.skuInfo?.title }}</h5>
            </div>
            <div class="price">
              <p class="allprice">
                ￥{{
                  list?.skuInfo?.skus[0]?.price
                    .toString()
                    .substring(
                      0,
                      list?.skuInfo?.skus[0]?.price.toString().length - 2
                    )
                }}
              </p>
              <div class="add">
                <span>查看商品</span>
              </div>
            </div>
          </div>
          <template #right>
            <van-button
              square
              text="删除"
              type="danger"
              class="delete-button"
              @click="deleteCollectionEl({ id: list }), savehistoryEl()"
            />
          </template>
        </van-swipe-cell>
      </li>
    </ul>
    <div class="bg" v-else></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["collectional"]),
  },
  methods: {
    ...mapMutations(["deleteCollectionEl", "savehistoryEl"]),
  },
};
</script>

<style lang="less" scoped>
.collectional {
    width: 375rem;
    height: 100vh;
    background: #eaeaea;
  .title {
    width: 375rem;
    height: 40rem;
    margin: 0rem auto;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25rem 15rem;
    border-bottom: 1rem solid rgb(221, 221, 221);
    box-sizing: border-box;
    i {
      font-size: 24rem;
    }
    p {
      font-size: 17rem;
    }
    span {
      width: 24rem;
      height: 24rem;
      background-image: url(../assets/buyCar.png);
      background-size: 100%;
      background-repeat: no-repeat;
      display: inline-block;
    }
  }
  .collect {
    background: #fff;
    .list {
      width: 350rem;
      padding: 10rem 0;
      margin: 0 auto;
      border-bottom: 0.5rem solid rgb(221, 221, 221);
      background: #fff;
      .storetop {
        display: flex;
        align-items: center;
        background: #fff;
        margin-bottom: 10rem;
        .img {
          width: 20rem;
          height: 20rem;
          background-size: 100%;
          border-radius: 50%;
        }
        h3 {
          font-size: 14rem;
          margin-left: 5rem;
          color: black;
          text-align: left;
        }
      }
      .gooditem {
        display: flex;
        text-align: left;
        position: relative;
        .goodphoto {
          width: 70rem;
          height: 80rem;
          border-radius: 5rem;
          margin-right: 10rem;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
        }
        .text {
          h5 {
            width: 220rem;
            height: 38rem;
            font-size: 13.5rem;
            text-overflow: -o-ellipsis-lastline;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          p {
            margin: 5rem 0;
            width: 170rem;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
        }
        .price {
          width: 265rem;
          position: absolute;
          bottom: 0rem;
          left: 80rem;
          text-align: right;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .allprice {
            font-size: 17rem;
            color: rgb(255, 48, 117);
          }
          .add {
            background: rgba(119, 119, 119, 0.1);
            padding: 3rem 5rem;
            border-radius: 50rem;
            margin-left: 10rem;
            color: rgb(255, 48, 117);
            display: flex;
            align-items: center;
            justify-content: space-between;
            span {
              margin-left: 5rem;
            }
          }
        }
      }
      .delete-button {
        height: 100%;
      }
    }
  }
  .bg {
    width: 375rem;
    height: 250rem;
    background-image: url(../assets/collectionbg.jpg);
    background-size: 100%;
    background-color: #fff;
    background-repeat: no-repeat;
  }
}
</style>