<template>
  <div class="strategic-cooperation">
    <video autoplay id="videoElement"></video>
    <div class="graphic">
      <div class="top">
        <div class="seller">
          <div class="img" :style="{
            backgroundImage:
              'url(' +
              `${livingroom?.actorInfo?.avatar}_280x374.v1cAC.40.webp` +
              ')',
          }"></div>
          <div class="name">
            <h2>{{ livingroom?.actorInfo?.uname }}</h2>
            <span>热度 {{ livingroom?.onlineUserCount }}</span>
          </div>
          <div class="btn">关注</div>
        </div>
      </div>
      <div class="barrage">
        <ul :style="{ marginTop:`${130 + value}rem`}">
          <li v-for="(comment, i) in allcomments" :key="i">
            <p><span class="higit">{{comment.username}} </span> {{comment.action}}</p>
          </li>
        </ul>
      </div>
      <div class="bottom">
        <div class="left">
          <div class="cssbag" @click="promotemenu = !promotemenu">
            <span>{{promotegoods.length}}</span>
          </div>
          <p>说点什么...</p>
        </div>
        <div class="right">
          <div class="share"></div>
          <div class="praise"></div>
        </div>
      </div>
      <van-popup :overlay="false" v-model="promotemenu" position="bottom"
        :style="{ height: '65%', background:'transparent' }">
        <div class="promotemenu">
          <h3>全部商品 {{promotegoods.length}}</h3>
          <ul class="promotelist">
            <li v-for="(promotegood, i) in promotegoods" :key="i">
              <div class="goodphoto" :style="{ backgroundImage: 'url(' + `${promotegood.image}` + ')' }"></div>
              <div class="text">
                <div class="title">
                  <p class="kill">秒杀中</p>
                  <h5>{{ promotegood.title }}</h5>
                </div>
                <div class="price">
                  <div class="allprice">
                    <div class="oldprice">
                      ￥{{
                      promotegood.originalPrice
                      .toString()
                      .substring(
                      0,
                      promotegood?.originalPrice.toString().length - 2
                      )
                      }}
                    </div>
                    <div class="nowprice">
                      ￥{{
                      promotegood.defaultPrice
                      .toString()
                      .substring(
                      0,
                      promotegood?.defaultPrice.toString().length - 2
                      )
                      }}
                    </div>

                  </div>
                  <div class="add">
                    <span>去购买</span>
                  </div>
                </div>
              </div>
            </li>
          </ul>
          <div class="promotemenumask" @click="promotemenu = !promotemenu"></div>
        </div>
      </van-popup>
      <!-- 返回键 -->
      <div class="reback" @click="$router.back()">
        <svg t="1665450673437" class="icon" viewBox="0 0 1027 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
          p-id="10851" width="22" height="22">
