            p-id="10852" fill="#dee1e6"></path>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import flvjs from "flv.js";

export default {
  data: function () {
    return {
      livingroom: {},
      allcomments: [],
      promotegoods: [],
      showlist: "",
      value: 0,
      k: 0,
      promotemenu: false,
    };
  },

  mounted() {
    this.showlist = setInterval(() => {
      this.value -= 32.5;
      this.k++;
      this.allcomments.push(this.allcomments[this.k]);
    }, 2500);
  },

  beforeDestroy() {
    clearInterval(this.showlist)
  },

  methods: {
    // 直播媒体流
    createVideo() {
      if (flvjs.isSupported()) {
        var videoElement = document.getElementById("videoElement");
        var flvPlayer = flvjs.createPlayer({
          type: "flv",
          url: this.livingroom.liveUrl, //直播的的url地址
          // url: this.livingroom?.definitions[3]?.liveUrl, //流畅画质(数据不稳定)
        });
        flvPlayer.attachMediaElement(videoElement);
        flvPlayer.load();
        flvPlayer.play();
      }
    },

    livingroomEl({ roomId, actorId }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/live/actorLiveInfo", {
          params: { roomId, actorId },
        })
        .then((res) => {
          this.livingroom = res.data.data;
          this.createVideo(); //axios获取成功后, 调用直播媒体流
          this.PromotegoodsEl({ roomId, actorId });
          // console.log(roomId, actorId, this.livingroom);
        });
    },

    //促销商品
    PromotegoodsEl({ roomId, actorId }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/live/queryDiscountPrice", {
          params: { roomId, actorId },
        })
        .then((res) => {
          this.promotegoods = res.data.data.itemList;
        });
    },

    // 直播弹幕
    commentsEl() {
      this.axios
        .get("https://apis.netstart.cn/mogujie/live/comments")
        .then((res) => {
          this.allcomments = res.data;
          // console.log(res.data);
        });
    },
  },

  created() {
    this.livingroomEl({
      roomId: this.$route.query.roomId,
      actorId: this.$route.query.actorId,
    });
    this.commentsEl();
  },
  watch: {
    value: function () {
      this.value;
    },
    allcomments: function () {
      this.allcomments;
    },
  },

};
</script>

<style scoped lang="less">
