<template>
  <div v-if="!open">
    <SearchTwo class="top" />
    <ul class="navs">
      <li @click="
        SearchResult({ q: history[0], sort: 'pop' }),
          (Resultsort = 'pop'),
          (drop = false)
      " :class="{ colorEl: Resultsort == 'pop' }">
        {{ navFilter[0]?.title }}
      </li>
      |
      <li @click="
        SearchResult({ q: history[0], sort: 'sell' }),
          (Resultsort = 'sell'),
          (drop = false)
      " :class="{ colorEl: Resultsort == 'sell' }">
        {{ navFilter[1]?.title }}
      </li>
      |
      <li @click="
        SearchResult({ q: history[0], sort: 'new' }),
          (Resultsort = 'new'),
          (drop = false)
      " :class="{ colorEl: Resultsort == 'new' }">
        {{ navFilter[2]?.title }}
      </li>
      |
      <li class="price" :class="{ colorEl: Resultsort == 'price' }" @click="(Resultsort = 'price'), (drop = !drop)">
        价格<i class="fa"></i>
      </li>
      <div class="dropdown" v-if="drop">
        <ul class="btn">
          <li v-for="(pf, i) in priceFilter" :key="i" @click="
            SearchResult({
              q: history[0],
              sort: resultsort,
              minPrice: pf.min,
              maxPrice: pf.max,
            }),
              (drop = false)
          ">
            {{ pf.min }}-{{ pf.max }}
          </li>
        </ul>
        <div class="write">
          <span>区间(元)</span>
          <div class="input">
            <input type="text" v-model="minvalue" />
            <span class="line"></span>
            <input type="text" v-model="maxvalue" />
          </div>

          <p @click="
            SearchResult({
              q: history[0],
              sort: resultsort,
              minPrice: minvalue,
              maxPrice: maxvalue,
            }),
              (drop = false)
          ">
            确认
          </p>
        </div>
      </div>
    </ul>
    <!-- <van-list @load="onLoad" > -->
    <ul class="goodlist" v-if="results.length > 0">
      <li v-for="(result, i) in results" :key="i" @click="
        $router.push({ path: '/goodspage', query: { itemId: result.iid } })
      ">
        <div class="img" :style="{
          backgroundImage:
            'url(' +
            `${result.img}?imageView=1&type=webp&thumbnail=100x0` +
            ')',
        }">
          <i></i>
        </div>
        <p class="title">
          <span v-if="result?.leftbottom_taglist[0]?.content">{{
          result?.leftbottom_taglist[0]?.content
          }}</span>{{ result.title }}
        </p>
        <div class="price">
          <span>￥{{ result.price }}</span>
          <p>{{ result.cfav }} <i></i></p>
        </div>
      </li>
    </ul>
    <!-- </van-list> -->
    <p class="prompt" v-else>亲,未找到相关商品~</p>
  </div>
</template>

<script>
import SearchTwo from "@/components/SearchTwo.vue";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  components: {
    SearchTwo,
  },
  data: function () {
    return {
      Resultsort: "pop",
      drop: false,
      minvalue: "",
      maxvalue: "",
      page: 1,
    };
  },
  computed: {
    ...mapState([
      "open",
      "results",
      "navFilter",
      "history",
      "resultsort",
      "priceFilter",
    ]),
  },
  methods: {
    ...mapMutations(["openEl", "goodcontentEl"]),
    ...mapActions(["SearchResult"]),


    //加载更多(未实现)
    // onLoad() {
    //   this.page++;
    //   // this.SearchResult({
    //   //   q: this.history[0],
    //   //   sort: this.resultsort,
    //   //   page: this.page,
    //   // });
    //   this.axios.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
    //     params: {
    //       q: this.history[0],
    //       sort: this.resultsort,
    //       page: this.page,
    //     }
    //   })
    //     .then((res) => {
    //       this.results.push(...res.data.result.wall.docs);
    //     });
    // },

  },
  watch: {
    Resultsort: function () {
      return this.Resultsort;
    },
    results: function () {
      return this.results;
    },
  },
};
</script>

<style lang="less" scoped>
.top {
  width: 375rem;
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  z-index: 9;
}

.navs {
  width: 375rem;
  height: 40rem;
  background: #fff;
  color: rgb(197, 197, 197);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1rem solid rgb(198, 198, 198);
  position: fixed;
  top: 51rem;
  left: 0;
  background: #fff;
  z-index: 9;

  li {
    width: 93rem;
    font-size: 15.5rem;
    color: rgb(65, 65, 65);

    &.colorEl {
      color: red;
    }

    .fa {
      display: inline-block;
      width: 10rem;
      height: 10rem;
      margin: 3rem 8rem;
      border-left: 1rem solid rgb(197, 197, 197);
      border-bottom: 1rem solid rgb(197, 197, 197);
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
    }
  }

  .dropdown {
    width: 375rem;
    height: 110rem;
    position: absolute;
    top: 38rem;
    left: 0;
    background: #fff;
    color: black;
    z-index: 9;

    .btn {
      width: 360rem;
      display: flex;
      justify-content: space-between;
      margin: 20rem auto;

      li {
        width: 115rem;
        background: #f3f3f3;
        padding: 6rem;
      }
    }

    .write {
      width: 360rem;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;

      span {
        font-size: 14.8rem;
        margin-right: 5rem;
      }

      input {
        width: 90rem;
        height: 30rem;
        border: 1rem solid rgb(225, 225, 225);
        border-radius: 3rem;
      }

      .line {
        width: 12rem;
        border-bottom: 1rem solid rgb(225, 225, 225);
        display: inline-block;
        margin: 0 8rem;
        position: relative;
        top: -5rem;
        left: 0;
      }

      p {
        width: 80rem;
        height: 30rem;
        background: #ff5b76;
        padding: 0rem 10rem;
