        color: #fff;
        border-radius: 3rem;
      }
    }
  }
}

.goodlist {
  width: 375rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  background: #efefef;
  padding: 10rem 0rem;
  margin-top: 90rem;

  li {
    width: 175rem;
    // height: 260rem;
    background: rgb(255, 255, 255);
    margin-left: 8rem;
    margin-bottom: 8rem;
    border-radius: 5rem;
    padding-bottom: 5rem;

    .img {
      width: 175rem;
      height: 200rem;
      background-size: cover;
      background-repeat: no-repeat;
      border-radius: 5rem 5rem 0 0;
      position: relative;

      i {
        width: 23rem;
        height: 23rem;
        background-image: url(../assets/放大镜.png);
        background-size: 100%;
        display: inline-block;
        position: absolute;
        bottom: 5rem;
        right: 5rem;
      }
    }

    .title {
      width: 155rem;
      height: 32rem;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin: 3rem auto;
      text-align: left;

      span {
        background: #f0f0f0;
        padding: 1rem 2rem;
        border-radius: 2rem;
        margin-right: 5rem;
        font-size: 5rem;
        color: rgb(180, 180, 180);
        font-family: "宋体";
      }
    }

    .price {
      width: 155rem;
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      margin: 5rem auto;

      span {
        font-size: 17.5rem;
        font-weight: bold;
      }

      p {
        display: flex;
        align-items: center;
        color: rgb(108, 108, 108);

        i {
          width: 13rem;
          height: 13rem;
          background-image: url(../assets/mini-star.png);
          background-repeat: no-repeat;
          background-size: 100%;
          display: inline-block;
          margin-left: 3rem;
        }
      }
    }
  }
}

.prompt {
  margin: 20rem;
  font-size: 15rem;
}
</style>