import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../views/HomePage.vue'
import HomeView from '../views/HomeView.vue'
import MallView from '../views/MallView.vue'
import LiveView from '../views/LiveView.vue'
import UserView from '../views/UserView.vue'

import VideoPage from '../views/VideoPage.vue'
import SearchView from '../views/SearchView.vue'
import SideBar from '../views/SideBar.vue'
import GoodsPage from '../views/GoodsPage.vue'
import BarGoodList from '../views/BarGoodList.vue'
import ShoppingCar from '../views/ShoppingCar.vue'
import NowBuy from '../views/NowBuy.vue'
import LandingView from '../views/LandingView.vue'
import CommentsPage from '../views/CommentsPage.vue'
import LivePage from '../views/LivePage.vue'
import OrderPage from '../views/OrderPage.vue'
import CollectionalView from '../views/CollectionalView.vue'


Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    component: HomePage,
    children: [
      { path: "", component: HomeView },
      { path: "mall", component: MallView },
      { path: "live", component: LiveView },
      { path: "user", component: UserView },
    ]
  },

  {
    path: '/videopage',
    component: VideoPage
  },
  {
    path: '/searchview',
    component: SearchView
  },
  {
    path: '/sidebar',
    component: SideBar
  },
  {
    path: '/goodspage',
    component: GoodsPage,
  },
  {
    path: '/bargoodlist',
    component: BarGoodList,
  },
  {
    path: '/shoppingcar',
    component: ShoppingCar,
  },
  {
    path: '/nowbuy',
    component: NowBuy,
  },
  {
    path: '/landing',
    component: LandingView,
  },
  {
    path: '/comments',
    component: CommentsPage,
  },
  {
    path: '/livepage',
    component: LivePage,
  },
  {
    path: '/orderpage',
    component: OrderPage,
  },
  {
    path: '/collectional',
    component: CollectionalView,
  },


]

const router = new VueRouter({
  routes
})

export default router
