<template>
  <div class="mall">
    <header>
      <div class="left">
        <img src="../assets/mogu-logo.png" alt="" />
        <div class="text">
          <h2>蘑菇街 总有高手帮你挑</h2>
          <p>
          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <span>超过2亿用户已下载</span>
          </p>
        </div>
      </div>
      <div class="btn">前往查看</div>
    </header>
    <SearchOne />
    <ul class="shopNav">
      <li class="img" v-for="(nav, i) in shopNav" :key="i" :style="{
        backgroundImage:
          'url(' +
          `${nav.backgroundImage}?imageView=1&type=webp&thumbnail=100x0` +
          ')',
      }">
        <p>{{ nav.title }}</p>
      </li>
      <van-count-down #default="timeData" :time="time">
        <div class="times">
          <span class="block">{{ timeData.hours > 9 ? timeData.hours : "0" + timeData.hours }}</span>
          <span class="colon">:</span>
          <span class="block">{{ timeData.minutes > 9 ? timeData.minutes : "0" + timeData.minutes }}</span>
          <span class="colon">:</span>
          <span class="block">{{ timeData.seconds > 9 ? timeData.seconds : "0" + timeData.seconds }}</span>
        </div>
      </van-count-down>
    </ul>
    <p class="love"></p>
    <p v-if="loading">
      <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
        style="margin: auto; background: #fff; display: block" width="200px" height="200px" viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid">
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44"
            begin="-0.5s"></animate>
        </circle>
        <circle cx="56" cy="50" fill="#46dff0" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44" begin="0s">
          </animate>
        </circle>
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44"
            begin="-0.5s"></animate>
          <animate attributeName="fill-opacity" values="0;0;1;1" calcMode="discrete" keyTimes="0;0.499;0.5;1" dur="1s"
            repeatCount="indefinite"></animate>
        </circle>
      </svg>
    </p>
    <van-list v-model="loadingadd" :finished="finished" @load="onLoad">
      <ul class="Guesslike">
        <li v-for="(like, index) in Guesslike" :key="index"
          @click="$router.push({ path: '/goodspage', query: { itemId: like.iid } })">
          <div class="img" :style="{
            backgroundImage:
              'url(' +
              `${like.img}?imageView=1&type=webp&thumbnail=100x0` +
              ')',
          }"></div>
          <p class="title">{{ like.title }}</p>
          <div class="price">
            <span>￥{{ like.price }}</span>
            <p>{{ like.cfav }} <i></i></p>
          </div>
          <p class="BuyNow">立即购买</p>
        </li>
      </ul>
    </van-list>

  </div>
</template>

<script>
import SearchOne from "@/components/SearchOne.vue";
import { mapMutations } from "vuex";

export default {
  components: {
    SearchOne,
  },
  data: function () {
    return {
      shopNav: [],
      Guesslike: [],
      time: 30 * 60 * 60 * 1000,
      loading: true,
      loadingadd: false,
      page: 0,
      finished:false
    };
  },
  methods: {
    ...mapMutations(["goodcontentEl", "shopNavEl", "GuesslikeEl"]),

    // 用于加载更多
    onLoad() {
      // 猜你喜欢
      this.page += 1;
      this.axios
        .get("https://apis.netstart.cn/mogujie/goods/personalized", {
          params: { page: this.page }
        })
        .then((res) => {
          this.Guesslike.push(...res.data.data.wall.docs);
          this.loadingadd = false;
          this.loading = false;
        });

        if (this.Guesslike.length > 40) {
          this.finished = true;
        }
    },
  },

  created() {
    // 商城导航
    this.axios
      .get("https://apis.netstart.cn/mogujie/shopNav.json")
      .then((res) => {
        this.shopNav = res.data;
      });
  },
};
</script>

