<template>
  <div class="orderpage">
    <div class="title">
      <i class="fa fa-angle-left" @click="$router.push({ path: '/user' })"></i>
      <p>我的订单</p>
      <span @click="$router.push({ path: '/shoppingcar' })"></span>
    </div>
    <ul class="listnav">
      <li @click="current = 'all'" :class="{ active: current == 'all' }">
        全部
      </li>
      <li @click="current = 'pay'" :class="{ active: current == 'pay' }">
        待付款
      </li>
      <li @click="current = 'send'" :class="{ active: current == 'send' }">
        待发货
      </li>
      <li
        @click="current = 'collect'"
        :class="{ active: current == 'collect' }"
      >
        待收货
      </li>
    </ul>
    <ul class="orderlist" v-if="current == 'send'">
      <li class="list" v-for="(list, i) in orderlist" :key="i">
        <van-swipe-cell>
          <div class="storetop">
            <div
              class="img"
              :style="{
                backgroundImage: 'url(' + `${list?.logo}` + ')',
              }"
            ></div>
            <h3>{{ list?.seller }}</h3>
          </div>
          <div class="gooditem">
            <div
              class="goodphoto"
              :style="{
                backgroundImage: 'url(' + `${list?.img}` + ')',
              }"
            ></div>
            <div class="text">
              <h5>{{ list?.text }}</h5>
              <div class="Sizecolor">
                <span v-if="list?.color">颜色:{{ list?.color }};</span>
                <span v-if="list?.sise"> 尺码:{{ list?.sise }}</span>
              </div>

              <p v-if="list?.courier">
                快递将从 {{ list?.courier }}, 发往目的地
              </p>
              <p v-else>包裹正在揽收</p>
            </div>
            <div class="price">
              <p class="allprice">￥{{ list?.price.toString().substring(0, list.price.toString().length - 2) }}</p>
              <p>x{{ list.num }}</p>
            </div>
          </div>
          <template #right>
            <van-button
              square
              text="删除"
              type="danger"
              class="delete-button"
              @click="deleteorderEl({ list }), savehistoryEl()"
            />
          </template>
        </van-swipe-cell>
      </li>
    </ul>
    <div class="bg pay" v-else-if="current == 'pay'">
      <div class="prompt">
        <p>你还没有相关的订单</p>
        <span>可以去看看有哪些想买的</span>
      </div>
    </div>
    <div class="bg send" v-else-if="current == 'all'">
      <div class="prompt">
        <p>你还没有相关的订单</p>
        <span>可以去看看有哪些想买的</span>
      </div>
    </div>
    <div class="bg collect" v-else-if="current == 'collect'">
      <div class="prompt">
        <p>你还没有相关的订单</p>
        <span>可以去看看有哪些想买的</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data: function () {
    return {
      current: "",
    };
  },
  computed: {
    ...mapState(["orderlist"]),
  },
  methods: {
    ...mapMutations(["savehistoryEl", "deleteorderEl"]),
  },
  created() {
    this.savehistoryEl();
    this.current = this.$route.query.current;
  },
};
</script>

<style lang="less" scoped>
.orderpage {
  width: 375rem;
  height: 100vh;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  .title {
    width: 350rem;
    height: 40rem;
    margin: 0 auto;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25rem 0;
    i {
      font-size: 24rem;
    }
    p {
      font-size: 17rem;
    }
    span {
      width: 24rem;
      height: 24rem;
      background-image: url(../assets/buyCar.png);
      background-size: 100%;
      background-repeat: no-repeat;
      display: inline-block;
    }
  }
  .listnav {
    height: 35rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    // padding: 10rem 0;
    border-bottom: 1rem solid rgb(226, 226, 226);
    li {
      width: 25%;
      font-size: 14rem;
      border-bottom: 1rem solid transparent;
      line-height: 35rem;
      &.active {
        border-bottom: 1rem solid red;
        color: red;
      }
    }
  }
  .orderlist {
    width: 375rem;
    .list {
      width: 350rem;
      padding: 10rem 0;
      margin: 0 auto;
      border-bottom: 0.5rem solid rgb(221, 221, 221);

      .storetop {
        display: flex;
        align-items: center;
        background: #fff;
        margin-bottom: 10rem;

        .img {
          width: 20rem;
          height: 20rem;
          background-size: 100%;
          border-radius: 50%;
        }

        h3 {
          font-size: 14rem;
          margin-left: 5rem;
          color: black;
        }
      }

      .gooditem {
        display: flex;
        text-align: left;
        position: relative;
        .goodphoto {
          width: 70rem;
          height: 85rem;
          border-radius: 5rem 5rem 0 0;
          margin-right: 10rem;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
        }
        .text {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          h5 {
            width: 185rem;
            height: 38rem;
            font-size: 13.5rem;
            text-overflow: -o-ellipsis-lastline;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          .Sizecolor {
            width: 170rem;
            margin: 5rem 0;
          }
          p {
            margin: 5rem 0;
            width: 170rem;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
        }
        .price {
          position: absolute;
          bottom: 0rem;
          right: 12rem;
          text-align: right;
          .allprice {
            font-size: 17rem;
            color: rgb(255, 48, 117);
          }
          p {
            font-size: 5rem;
            color: gray;
          }
        }
      }
      .delete-button {
        height: 100%;
      }
    }
  }
  .bg {
    width: 375rem;
    height: 240rem;
    background-image: url(../assets/orderbg.png);
    background-size: 100%;
    background-repeat: no-repeat;
    border-bottom: 1rem solid rgb(221, 221, 221);
    position: relative;
    .prompt {
      position: absolute;
      top: 180rem;
      left: 30%;
      p {
        font-size: 15rem;
        letter-spacing: 2.5rem;
        margin: 5rem 0;
      }
      span {
        color: gray;
      }
    }
  }
}
</style>