            <van-icon size="22" color="#fff" name="shopping-cart-o" />
          </li>
        </ul>
      </div>

      <div class="colorclass" v-if="goodcontent?.skuBarInfo?.list">
        <div
          class="firsticon"
          :style="{
            backgroundImage:
              'url(' + `${goodcontent?.skuInfo?.skus[0]?.img}` + ')',
