              backgroundImage: 'url(' + `${lis.image}` + ')',
            }"
          ></li>
        </ul>
      </div>

      <div class="text">
        <div class="price">
          <p>
            ￥<span v-if="goodcontent?.normalPriceV2?.nowPrice"
