          </p>
          <div class="selled">已售 {{ goodcontent?.normalPriceV2?.sales }}</div>
        </div>
        <div class="maintext">
          <p>{{ goodcontent?.normalShareInfo?.title }}</p>
          <div class="help">
            <img src="../assets/miniman.png" alt="" /> 帮我选
          </div>
        </div>
      </div>

      <div class="choose" @click="(show = true), (kkk = '购物车')">
        <p>选择 <span>尺码、颜色</span></p>
        <i class="into"></i>
      </div>
      <!-- 邮费 -->
      <div class="Package">
        <p>{{ goodcontent?.itemServices?.columns[1]?.desc }}</p>
        <span>{{ goodcontent?.itemServices?.columns[2]?.desc }}</span>
      </div>

      <div class="service">
