<script>
import { mapState, mapMutations } from "vuex";
export default {
  data: function () {
    return {
      collected: "",
      show: false,
      current: 0,
      value: 0,
      star: 5,
      pactive: "one",
      goodscar: {},
      color: "",
      sise: "",
      kkk: "购物车",
      imgindex: 1,
      selected: false,
      selectedcolor: "",
      selectedsise: "",
      goodcontent: {},
    };
  },

  computed: {
    ...mapState(["collectional"]),
  },

  methods: {
    ...mapMutations([
      "shoppingcarEl",
      "savehistoryEl",
      "buynowEl",
      "collectionEl",
    ]),

    onChange(index) {
      this.current = index;
    },

    addgoods: function () {
      this.goodscar = {
        color: this.color,
        sise: this.sise,
        num: this.value,
      };
      console.log(this.goodscar);
    },

    imgindexEl: function (i) {
      if (this.goodcontent?.skuInfo?.props[1]?.list?.length > 0) {
        this.imgindex = i * this.goodcontent?.skuInfo?.props[1]?.list?.length;
      } else {
        this.imgindex = i * 1;
      }
    },

    goodcontentEl({ itemId }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/goods/detail", {
          params: { itemId },
        })
        .then((res) => {
          this.goodcontent = res.data.data.result;
          this.collectionchange();
          console.log(this.goodcontent);
        });
    },

    collectionchange: function () {
      if (
        this.collectional.find(
          (o) => o.itemInfo.itemId === this.goodcontent.itemInfo.itemId
        )
      ) {
        this.collected = true;
      } else {
        this.collected = false;
      }
    },
  },
  created() {
    this.goodcontentEl({ itemId: this.$route.query.itemId });
  },
};
</script>

