        width="200px"
        height="200px"
        viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid"
      >
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="-0.5s"
          ></animate>
        </circle>
        <circle cx="56" cy="50" fill="#46dff0" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="0s"
          ></animate>
        </circle>
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="-0.5s"
          ></animate>
          <animate
            attributeName="fill-opacity"
            values="0;0;1;1"
            calcMode="discrete"
            keyTimes="0;0.499;0.5;1"
            dur="1s"
            repeatCount="indefinite"
          ></animate>
        </circle>
      </svg>
    </p>
    <div class="comments" v-else-if="goodcontent?.list?.length > 0">
      <div class="allscore">
        综合评分 <span> {{ goodcontent.averageScore }}</span>
      </div>
      <ul class="icon">
        <li @click="index = -1" :class="{ iconcolor: index === -1 }">全部</li>
        <li
          v-for="(rate, i) in goodcontent.topRateTags.slice(1)"
          :key="i"
          @click="index = i"
          :class="{ iconcolor: index === i }"
        >
          {{ rate.value }}({{ rate.num }})
        </li>
      </ul>

      <ul class="miniicon">
        <li v-for="(rate, i) in goodcontent.rateTags" :key="i">
          {{ rate.value }} {{ rate.num }}
        </li>
      </ul>

      <div class="sorting"><span>按时间排序</span> | <span>默认排序</span></div>
