            </div>
          </div>
          <div class="text">
            {{ list.content }}
          </div>
          <div class="time">
            <p>2020.9.18</p>
            <div class="btn">
              <i>赞</i>
              <span>有用</span>
            </div>
          </div>
        </li> -->

        <li v-for="(list, i) in goodcontent.list" :key="i">
          <div class="top">
            <img :src="list.user.avatar" alt="" />
            <div class="actor">
              <span>{{ list.user.uname }}</span>
              <p>{{ list.style }}</p>
            </div>
          </div>
          <div class="text" v-if="list.content !== ''">
            {{ list.content }}
          </div>
          <div class="text" v-else>此用户没有填写评价</div>

