<script>
// import { mapState } from "vuex";
export default {
  data: function () {
    return {
      goodcontent: {},
      zan: true,
      index: -1,
      loading: true,
    };
  },
  methods: {
    goodcommentEl({ itemId }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/goods/commonRateList", {
          params: { itemId },
        })
        .then((res) => {
          this.goodcontent = res.data.data;
          // console.log(this.goodcontent);
          this.loading = false;
        });
    },
  },

  created() {
    this.goodcommentEl({ itemId: this.$route.query.itemId });
  },
};
</script>

