<template>
  <div class="shopingcar">
    <div class="hearder">
      <span @click="$router.back()"></span>
      <p>购物车</p>
      <div class="editor">
        <h4 @click="show = !show" :class="{ heightlighing: show !== true }">
          编辑
        </h4>
        <a href=""></a>
      </div>
    </div>
    <div class="shoppingarea">
      <ul v-if="shoppingcar.length > 0">
        <li v-for="(s, i) in shoppingcar" :key="i">
          <div class="flex">
            <input
              type="checkbox"
              name=""
              id=""
              class="pre"
              :checked="s.finish"
              @click="prefinishEl({ id: s, finish: $event.target.checked })"
            />
            <div
              class="picture"
              :style="{
                backgroundImage: 'url(' + `${s.img}` + ')',
              }"
            ></div>
            <div class="cardinfor">
              <div class="top">
                <img :src="s.logo" alt="" />
                <p>{{ s.seller }}</p>
              </div>
              <div class="text">{{ s.text }}</div>
              <p class="colorsise">
                <span v-if="s.color">颜色: {{ s.color }};</span
                ><span v-if="s.sise"> 尺码: {{ s.sise }};</span>
              </p>
            </div>
            <div class="checkicon" :class="{ active: s.finish }">
              <i class="fa fa-check"></i>
            </div>
          </div>
          <div class="price">
            <p>
              ￥{{
                s.price.toString().substring(0, s.price.toString().length - 2)
              }}
            </p>
            <van-stepper v-model="s.num" class="adds" />
          </div>
          <!-- <van-swipe-cell>
            <van-card
              :num="s.num"
              :price="s.price"
              :desc="s.color"
              :title="s.text"
              class="goods-card"
              :thumb="s.img"
            />
            <template #tags>
              <van-tag plain type="danger">{{ s.color }}</van-tag>
              <van-tag plain type="danger">{{ s.sise }}</van-tag>
            </template>
            <template #right>
              <van-button
                square
                text="删除"
                type="danger"
                class="delete-button"
                @click="deletecarEl({ id: s })"
              />
            </template>
          </van-swipe-cell> -->

          <!-- <van-swipe-cell>
            <van-card
              num="2"
              price="2.00"
              desc="描述信息"
              :title="s.text"
              :thumb="s.img"
            >
              <template #tags>
                <van-tag plain type="danger">{{s.color}}</van-tag>
                <van-tag plain type="danger">{{s.sise}}</van-tag>
              </template>
              <template #footer>
                <van-button size="mini">-1</van-button>
                <van-button size="mini">+1</van-button>
              </template>
            </van-card>
          </van-swipe-cell> -->
        </li>
      </ul>
      <div class="bg" v-else>
        <div class="carbg"></div>
        <div class="nothing">
          <p>你的购物车空空如也</p>
          <div class="looking" @click="$router.push({ path: '/' })">去逛逛</div>
        </div>
      </div>
    </div>

    <div class="allLook">
      <p>大家都在看</p>
      <div class="lookbg"></div>
    </div>
    <div v-if="shoppingcar.length > 0">
      <div class="settlement" v-if="show">
        <div class="allcheckicon">
          <input
            type="checkbox"
            class="all"
            :checked="shoppingcar.every((o) => o.finish)"
            @change="allfinishEl({ finish: $event.target.checked })"
          />
          <span>全选</span>
          <p :class="{ allactive: shoppingcar.every((o) => o.finish) }">
            <i class="fa fa-check"></i>
          </p>
        </div>
        <div class="allprice">
          <p>
            合计: <span>￥{{ allnum() }}</span>
          </p>
          <div class="btn" v-if="allnum() == 0">结算</div>
          <div
            class="btn"
            v-else
            @click="
              $router.push({ path: '/nowbuy' }),
                buynowEl({ all: shoppingcar.filter((o) => o.finish === true) })
            "
          >
            结算
          </div>
        </div>
      </div>
      
      <div class="clearall" v-else>
        <div class="allcheckicon">
          <input
            type="checkbox"
            class="all"
            :checked="shoppingcar.every((o) => o.finish)"
            @change="allfinishEl({ finish: $event.target.checked })"
          />
          <span>全选</span>
          <p :class="{ allactive: shoppingcar.every((o) => o.finish) }">
            <i class="fa fa-check"></i>
          </p>
        </div>
        <div class="allclear">
          <div class="btn" @click="deletecarEl(), savehistoryEl()">删除</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data: function () {
    return {
      show: true,
    };
  },
  computed: {
    ...mapState(["shoppingcar", "prefinish"]),
  },
  methods: {
    ...mapMutations([
      "allfinishEl",
      "prefinishEl",
      "deletecarEl",
      "savehistoryEl",
      "buynowEl",
    ]),

    allnum: function () {
      return this.shoppingcar
        .map((o) => (o.finish ? o.price * o.num : 0))
        .reduce((total, num) => total + num)
        .toString()
        .substring(
          0,
          this.shoppingcar
            .map((o) => (o.finish ? o.price * o.num : 0))
            .reduce((total, num) => total + num)
            .toString().length - 2
        );
    },
  },
};
</script>

