<template>
  <div>
    <p v-if="loading">
      <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
        style="margin: auto; background: #fff; display: block" width="200px" height="200px" viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid">
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44"
            begin="-0.5s"></animate>
        </circle>
        <circle cx="56" cy="50" fill="#46dff0" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44" begin="0s">
          </animate>
        </circle>
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate attributeName="cx" repeatCount="indefinite" dur="1s" keyTimes="0;0.5;1" values="44;56;44"
            begin="-0.5s"></animate>
          <animate attributeName="fill-opacity" values="0;0;1;1" calcMode="discrete" keyTimes="0;0.499;0.5;1" dur="1s"
            repeatCount="indefinite"></animate>
        </circle>
      </svg>
    </p>
    <van-list v-model="loadingadd" @load="onLoad">
      <div class="bg">
        <div class="left">
          <ul v-for="(live, index) in liveplay" :key="index">
            <li v-if="index % 2 !== 0" @click="
              $router.push({
                path: '/videopage',
                query: {
                  itemId: live.itemIdUrl,
                  actorId: live.actorIdUrl,
                },
              }),
              copyEl({ all: live })
            ">
              <div class="videoimg">
                <img :src="`${live.originItemImage}_280x374.v1cAC.40.webp`" alt="" />
                <div class="actor">
                  <img :src="`${live.actorAvatar}_280x374.v1cAC.40.webp`" alt="" />
                  <span>{{ live.actorName }} {{ live.actorTag }}</span>
                </div>
                <p></p>
              </div>

              <div class="title">
                <p>{{ live.title }}</p>

                <div class="price">
                  <div class="priceleft">
                    <h4>
                      ￥<span>{{ parseInt(live.showDiscountPrice) }}</span>.{{
                          live.showDiscountPrice.toString().split(".")[1]
                      }}
                    </h4>
                    <img :src="live.bottomIcon" alt="" />
                  </div>
                  <div class="sales">
                    {{ live.sale }}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div class="right">
          <ul v-for="(live, index) in liveplay" :key="index">
            <li v-if="index % 2 == 0" @click="
              $router.push({
                path: '/videopage',
                query: {
                  itemId: live.itemIdUrl,
                  actorId: live.actorIdUrl,
                },
              }),
              copyEl({ all: live })
            ">
              <div class="videoimg">
                <img :src="`${live.originItemImage}_280x374.v1cAC.40.webp`" alt="" />
                <div class="actor">
                  <img :src="`${live.actorAvatar}_280x374.v1cAC.40.webp`" alt="" />
                  <span>{{ live.actorName }} {{ live.actorTag }}</span>
                </div>
                <p></p>
              </div>
              <div class="title">
                <p>{{ live.title }}</p>
                <div class="price">
                  <div class="priceleft">
                    <h4>
                      ￥<span>{{ parseInt(live.showDiscountPrice) }}</span>.{{
                          live.showDiscountPrice.toString().split(".")[1]
                      }}
                    </h4>
                    <img :src="live.bottomIcon" alt="" />
                  </div>
                  <div class="sales">
                    {{ live.sale }}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  data: function () {
    return {
      liveplay: [],
      loading: true,
      loadingadd: false,
      page: 1,
    };
  },
  methods: {
    ...mapMutations(["copyEl"]),
    // 用于加载更多
    onLoad() {
      this.axios
        .get("https://apis.netstart.cn/mogujie/video/item", {
          params: { page: this.page },
        })
        .then((res) => {
          this.page += 1;
          this.liveplay.push(...res.data.data.list);
          this.loading = false;
          this.loadingadd = false;
        });
    },
  },

  // created() {
  //   //视频回放
  //   this.axios
  //     .get("https://apis.netstart.cn/mogujie/video/item", {
  //       params: { page: 1 },
  //     })
  //     .then((res) => {
  //       this.liveplay = res.data.data.list;
  //       this.loading = false;
  //     });
  // },
};
</script>

<style lang="less" scoped>
