  width: 375rem;
  background: #ededed;
  padding: 10rem 0;
  display: flex;
  justify-content: space-between;
  margin: 0rem auto;

  .left {
    margin-left: 8rem;

    ul {
      li {
        width: 175rem;
        border-radius: 5rem;
        margin-bottom: 8rem;
        background: #ffffff;
        height: 100%;

        .videoimg {
          width: 100%;
          position: relative;

          img {
            width: 100%;
            border-radius: 5rem 5rem 0 0;
          }

          .actor {
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            align-items: center;

            img {
              width: 23rem;
              height: 23rem;
              border-radius: 50%;
              margin: 10rem 5rem 10rem 10rem;
            }

            span {
              color: #fff;
              font-size: 14rem;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }

          p {
            width: 30rem;
            height: 30rem;
            background-image: url(../assets/playicon.png);
            background-position: 0rem 0rem;
            background-size: 100%;
            background-repeat: no-repeat;
            position: absolute;
            top: 5rem;
            right: 5rem;
          }
        }

        .title {
          border-radius: 0 0 5rem 5rem;

          p {
            text-align: left;
            box-sizing: border-box;
            padding: 0rem 8rem;
            margin: 3rem 0;
            text-overflow: -o-ellipsis-lastline;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            font-size: 14rem;
          }

          .price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            box-sizing: border-box;
            padding: 5rem 8rem 10rem 8rem;
            color: #ff4466;

            .priceleft {
              display: flex;
              align-items: center;

              h4 {
                text-align: left;

                span {
                  font-size: 18.5rem;
                }
              }

              img {
                width: 22rem;
                margin-left: 5rem;
              }
            }

            .sales {
              padding: 1rem 5rem;
              background: #ffecef;
              border-radius: 10rem;
            }
          }
        }
      }
    }
  }

  .right {
    margin-right: 8rem;

    ul {
      li {
        width: 175rem;
