<template>
  <div>
    <SearchThreeTop :history="history"/>
    <ul class="navs">
      <li
        @click="
          SearchResult({ q: history, sort: 'pop' }),
            (Resultsort = 'pop'),
            (drop = false)
        "
        :class="{ colorEl: Resultsort == 'pop' }"
      >
        {{ navFilter[0]?.title }}
      </li>
      |
      <li
        @click="
          SearchResult({ q: history, sort: 'sell' }),
            (Resultsort = 'sell'),
            (drop = false)
        "
        :class="{ colorEl: Resultsort == 'sell' }"
      >
        {{ navFilter[1]?.title }}
      </li>
      |
      <li
        @click="
          SearchResult({ q: history, sort: 'new' }),
            (Resultsort = 'new'),
            (drop = false)
        "
        :class="{ colorEl: Resultsort == 'new' }"
      >
        {{ navFilter[2]?.title }}
      </li>
      |
      <li
        class="price"
        :class="{ colorEl: Resultsort == 'price' }"
        @click="(Resultsort = 'price'), (drop = !drop)"
      >
        价格<i class="fa"></i>
      </li>
      <div class="dropdown" v-if="drop">
        <ul class="btn">
          <li
            v-for="(pf, i) in priceFilter"
            :key="i"
            @click="
              SearchResult({
                q: history,
                resultsort,
                minPrice: pf.min,
                maxPrice: pf.max,
              }),
                (drop = false)
            "
          >
            {{ pf.min }}-{{ pf.max }}
          </li>
        </ul>
        <div class="write">
          <span>区间(元)</span>
          <div class="input">
            <input type="text" v-model="minvalue" /> -
            <input type="text" v-model="maxvalue" />
          </div>

          <p
            @click="
              SearchResult({
                q: history,
                resultsort,
                minPrice: minvalue,
                maxPrice: maxvalue,
              }),
                (drop = false)
            "
          >
            确认
          </p>
        </div>
      </div>
    </ul>
    <ul class="goodlist" v-if="results.length > 0">
      <li
        v-for="(result, i) in results"
        :key="i"
        @click="
          $router.push({ path: '/goodspage', query: { itemId: result.iid } })
        "
      >
        <div
          class="img"
          :style="{
            backgroundImage:
              'url(' + `${result.img}_280x374.v1cAC.40.webp` + ')',
          }"
        ></div>
        <p class="title">{{ result.title }}</p>
        <div class="price">
          <span>￥{{ result.price }}</span>
          <p>{{ result.cfav }} <i></i></p>
        </div>
      </li>
    </ul>
    <p v-else>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        style="margin: auto; background: #fff; display: block"
        width="200px"
        height="200px"
        viewBox="0 0 100 100"
        preserveAspectRatio="xMidYMid"
      >
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="-0.5s"
          ></animate>
        </circle>
        <circle cx="56" cy="50" fill="#46dff0" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="0s"
          ></animate>
        </circle>
        <circle cx="44" cy="50" fill="#ff3d81" r="3">
          <animate
            attributeName="cx"
            repeatCount="indefinite"
            dur="1s"
            keyTimes="0;0.5;1"
            values="44;56;44"
            begin="-0.5s"
          ></animate>
          <animate
            attributeName="fill-opacity"
            values="0;0;1;1"
            calcMode="discrete"
            keyTimes="0;0.499;0.5;1"
            dur="1s"
            repeatCount="indefinite"
          ></animate>
        </circle>
      </svg>
    </p>
  </div>
</template>

<script>
import SearchThreeTop from "@/components/SearchThreeTop.vue";
// import { mapState, mapActions } from "vuex";
export default {
  components: {
    SearchThreeTop,
  },
  props: {},
  data: function () {
    return {
      Resultsort: "pop",
      drop: false,
      minvalue: "",
      maxvalue: "",

      results:[],
      navFilter:[],
      priceFilter:[],
      resultsort:'',
      history:'',
    };
  },

  computed: {
    // ...mapState([
    //   // "results",
    //   // "navFilter",
    //   // "history",
    //   // "resultsort",
    //   // "priceFilter",
    // ]),
  },

  methods: {
    // ...mapActions(["SearchResult"]),
    SearchResult({ q, sort, minPrice, maxPrice }) {
      this.axios.get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
        params: { q, sort, minPrice, maxPrice }
      })
        .then((res) => {
          this.results = res.data.result.wall.docs;
          this.navFilter = res.data.result.sortFilter;
          this.priceFilter = res.data.result.priceFilter;
          this.resultsort = sort;
          this.history = q;
        });
    },
  },
  created () {
    this.SearchResult({ q: this.$route.query.q, sort: this.$route.query.sort  })
  },
};
</script>

<style lang="less" scoped>
.navs {
  width: 375rem;
  height: 40rem;
  background: rgb(235, 235, 235);
  display: flex;
  align-items: center;
  justify-content: space-between;

  li {
    width: 93rem;
    font-size: 15.5rem;
    .fa {
      display: inline-block;
      width: 10rem;
      height: 10rem;
      margin: 3rem 8rem;
      border-left: 1rem solid #454545;
      border-bottom: 1rem solid #2d2d2d;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
    }
  }
  .dropdown {
    width: 375rem;
    height: 110rem;
    position: absolute;
    top: 90rem;
    left: 0;
    background: #fff;
    color: black;
    .btn {
      width: 360rem;
      display: flex;
      justify-content: space-between;
      margin: 20rem auto;
      li {
        width: 110rem;
        background: #f3f3f3;
        padding: 6rem;
      }
    }
    .write {
      width: 360rem;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        font-size: 14.8rem;
      }
      input {
        width: 90rem;
        height: 30rem;
      }
      p {
        width: 80rem;
        height: 30rem;
        background: #ff5b76;
        padding: 0rem 10rem;
        line-height: 30rem;
        color: #fff;
      }
    }
  }
}
.goodlist {
  width: 375rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background: #efefef;
  padding: 20rem 15rem;
  li {
    width: 165rem;
    // height: 260rem;
    background: rgb(255, 255, 255);
    margin-bottom: 10rem;
    border-radius: 5rem;
    padding-bottom: 5rem;
    .img {
      width: 165rem;
      height: 200rem;
      background-size: cover;
      background-repeat: no-repeat;
      border-radius: 5rem 5rem 0 0;
    }
    .title {
      width: 155rem;
      height: 32rem;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin: 3rem auto;
      text-align: left;
    }
    .price {
      width: 155rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 5rem auto;
      span {
        font-size: 15rem;
        font-weight: bold;
      }
      p {
        display: flex;
        align-items: center;
        color: rgb(108, 108, 108);
        i {
          width: 13rem;
          height: 13rem;
          background-image: url(../assets/mini-star.png);
          background-repeat: no-repeat;
          background-size: 100%;
          display: inline-block;
          margin-left: 3rem;
        }
      }
    }
    p {
      font-size: 12rem;
    }
  }
}
.prompt {
  margin: 20rem;
  font-size: 15rem;
}
.colorEl {
  color: red;
}
</style>