<template>
  <div class="videopage">
    <video
      :src="Goodsdetails?.videoInfo?.url"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
      autoplay
      controls
    ></video>
    <span class="fa fa-times" @click="$router.back()"></span>
    <div class="list">
      <div
        class="photo"
        :style="{
          backgroundImage: 'url(' + `${Goodsdetails?.actorInfo?.avatar}` + ')',
        }"
      >
        <div class="attention" @click="attention = !attention">
          <p v-if="attention">关注</p>
          <div class="attentioned" v-else>
            <i class="fa fa-check-circle"></i>
          </div>
        </div>
      </div>
      <div class="star"></div>
      <div class="newicon"></div>
      <div class="shoppingcar"></div>
    </div>
    <div class="infor">
      <h4>@{{ Goodsdetails?.actorInfo?.name }}</h4>
      <p>
        <span>{{ Goodsdetails?.actorInfo?.height }}cm </span>
        <span> {{ Goodsdetails?.actorInfo?.weight }}kg</span> |
        <span>{{ Goodsdetails?.actorInfo?.fansCount }}</span>
      </p>
    </div>
    <div
      class="minigoods"
      @click="
        $router.push({
          path: '/goodspage',
          query: { itemId: Goodsdetails?.itemInfo.itemId },
        })
      "
    >
      <div
        class="img"
        :style="{
          backgroundImage:
            'url(' +
            `${Goodsdetails?.itemInfo?.cover}` +
            ')',
        }"
      ></div>

      <div class="price">
        <h3>{{ Goodsdetails?.itemInfo?.title }}</h3>
        <div class="icon">
          <p v-if="Goodsdetails.subTitle">待秒杀</p>
          <span v-if="Goodsdetails?.itemInfo?.discountPrice">￥{{ parseInt(Goodsdetails?.itemInfo?.discountPrice) }}</span>
        </div>
      </div>
      <div class="buy">立即购买</div>
    </div>
