<script>
import { mapState } from "vuex";
export default {
  data: function () {
    return {
      duration: null,
      currentTime: null,
      Goodsdetails: {},
      attention: true,
    };
  },

  computed: {
    ...mapState(["all"]),
  },
  methods: {
    //视频详情内容
    goodsdetailEl({ itemId, actorId }) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/video/detail", {
          params: { itemId, actorId },
        })
        .then((res) => {
          this.Goodsdetails = res.data.data.itemExplainList[0];
        });
    },
  },
  created() {
    //视频回放详情
    this.goodsdetailEl({
      itemId: this.$route.query.itemId,
      actorId: this.$route.query.actorId,
    });
  },
};
</script>

